<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :inline="true" :model="agency">
        <el-form-item label="机构代码:" prop="companyCode">
          <el-input v-model="agency.companyCode"></el-input>
        </el-form-item>
        <el-form-item label="投保人:" prop="policyHolder">
          <el-input v-model="agency.policyHolder"></el-input>
        </el-form-item>
        <el-form-item label="协议号:" prop="protocolCode">
          <el-input v-model="agency.protocolCode"></el-input>
        </el-form-item>
        <el-form-item label="归属业务员:" prop="salesMan">
          <el-input v-model="agency.salesMan"></el-input>
        </el-form-item>
        <el-form-item label="是否包含下级:" prop="isIncludeSub">
          <el-select v-model="agency.isIncludeSub">
            <el-option label="是" value="y"></el-option>
            <el-option label="否" value="n"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="排序:" prop="sort">
          <el-cascader
            expand-trigger="hover"
            :options="options"
            v-model="agency.sort">
          </el-cascader>
        </el-form-item>
        <el-form-item label="提交时间:" prop="submitTime">
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="请选择时间段"
              v-model="agency.submitTimeStart"
              style="width: 100%;">
            </el-date-picker>
          </el-col>
          <el-col style="text-align: center" :span="2">-</el-col>
          <el-col :span="11">
            <el-date-picker
              type="date"
              placeholder="请选择时间段"
              v-model="agency.submitTimeEnd"
              style="width: 100%;">
            </el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="申报状态:" prop="status">
          <el-checkbox-group v-model="agency.status">
            <el-checkbox label="待处理" name="1"></el-checkbox>
            <el-checkbox label="正在处理" name="2"></el-checkbox>
            <el-checkbox label="已处理未流转" name="3"></el-checkbox>
            <el-checkbox label="已处理流转" name="4"></el-checkbox>
            <el-checkbox label="已处理完毕" name="5"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button plain type="primary" size="small" @click="resetForm">重置</el-button>
      <el-button plain type="primary" size="small" @click="query">查询</el-button>
    </div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="curApprovalData"
          :pagination="true"
          :total="approvalData.length"
          rowKey="agencyNo"
          @approval="approval"
          @dataChange="dataChange"></base-table>
      </div>
    </div>
  </div>
</template>

<script>
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'index',
  components: {BaseTable},
  data () {
    return {
      currentPage: 1,
      pageSize: 5,
      agency: {
        companyCode: '',
        policyHolder: '',
        protocolCode: '',
        salesMan: '',
        isIncludeSub: 'y',
        sort: [],
        submitTimeStart: '',
        submitTimeEnd: '',
        status: []
      },
      options: [
        {
          value: 'level',
          label: '级别',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        },
        {
          value: 'firstSubmit',
          label: '第一次提交时间',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        },
        {
          value: 'company',
          label: '机构',
          children: [
            {
              value: 'asc',
              label: '升序'
            },
            {
              value: 'desc',
              label: '降序'
            }
          ]
        }
      ],
      columns: [
        {prop: 'agencyNo', width: 200, label: '代理协议号'},
        {prop: 'agent', width: 180, label: '代理人名称'},
        {prop: 'ownOrg', width: 260, label: '归属机构'},
        {prop: 'policyHolder', width: 120, label: '投保人'},
        {prop: 'createTime', width: 200, label: '流入时间'},
        {prop: 'level', width: 120, label: '级别'},
        {prop: 'status', width: 130, label: '申报状态'},
        {prop: 'approval', width: 120, label: '处理人'},
        {prop: 'submitter', width: 120, label: '提交人'},
        {prop: 'firstTime', width: 180, label: '第一次提交时间'},
        {prop: 'operation', width: 120, label: '操作', operation: true, operations: [{text: '审批', event: 'approval'}]}
      ],
      approvalData: [
        {
          agencyNo: '50000100001',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '初审',
          status: '待处理',
          statusType: '',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        },
        {
          agencyNo: '50000100002',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '初审',
          status: '待处理',
          statusType: '',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        },
        {
          agencyNo: '50000100003',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '中级',
          status: '正在处理',
          statusType: 'danger',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        },
        {
          agencyNo: '50000100004',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '中级',
          status: '已处理未流转',
          statusType: 'warning',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        },
        {
          agencyNo: '50000100005',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '初审',
          status: '已处理流转',
          statusType: 'info',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        },
        {
          agencyNo: '50000100006',
          agent: '晋投保险经纪有限公司',
          ownOrg: '山西省分公司太原支公司',
          policyHolder: '张翰',
          createTime: '2018/01/01',
          level: '初审',
          status: '已处理完毕',
          statusType: 'success',
          approval: '王伟',
          submitter: '王伟',
          firstTime: '2018-01-15 10:30:00'
        }
      ]
    }
  },
  computed: {
    curApprovalData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.approvalData.length ? this.approvalData.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.approvalData[i])
      }
      return result
    }
  },
  methods: {
    resetForm () {
      this.approval.status = []
    },
    query () {
      this.currentPage = 1
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    },
    approval () {
      this.$router.push({path: '/admin/agency/approval/approval'})
    }
  }
}
</script>

<style scoped>

</style>



